nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #5d777e;
  box-shadow: inset 0 0 30px #0c2e3b;
  height: 100%;
  width: 200px;
}

.nav-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nav-initials a {
  text-align: center;
  font-size: 3rem;
  margin-bottom: -20px;
  color: white;
  text-decoration: none;
  text-shadow: 1px 1px 0 black, -1px -1px 0 black, 1px -1px 0 black, -1px 1px 0 black;
}
.nav-initials a h1:active,
.nav-initials a h1.clicked {
  color: white;
  background-color: #5d777e;
  box-shadow: inset 0 0 5px #0c2e3b;
  padding: 5px 20px;
}

.links-container {
  visibility: hidden;
  text-align: center;
  overflow: hidden;
  max-height: 0;
  opacity: 0;
  transition: max-height 0.5s ease-out, opacity 0.5s ease-out, visibility 0s 0.5s;
  padding-left: 0px;
}

.links-container.is-active {
  visibility: visible;
  max-height: 500px;
  opacity: 1;
  transition: max-height 0.5s ease-out, opacity 0.5s ease-out, visibility 0s;
}

.pages-links {
  list-style-type: none;
  padding: 0;
  padding-bottom: 20px;
}
.pages-links li {
  width: 140px;
  margin-bottom: 20px;
  background-color: #548597;
  border-radius: 5%;
  padding: 5px;
  box-shadow: 0 2px 7px rgba(0, 0, 0, 0.3);
}
.pages-links li:active,
.pages-links li.clicked {
  color: white;
  background-color: #5d777e;
  box-shadow: inset 0 0 5px #0c2e3b;
}
.pages-links li a {
  color: white;
  text-decoration: none;
}

.nav-socials {
  text-align: center;
  padding-bottom: 10px;
}
.nav-socials a {
  font-size: 2rem;
  color: white;
  text-decoration: none;
}

.hamburger {
  position: relative;
  display: block;
  width: 50px;
  height: 35px;
  margin: 30px;
  cursor: pointer;
  appearance: none;
  background: none;
  outline: none;
  border: none;
}

.hamburger .bar, .hamburger:after, .hamburger:before {
  content: "";
  display: block;
  width: 100%;
  height: 5px;
  background-color: white;
  margin: 6px 0px;
  transition: 0.4s;
}

.hamburger.is-active .bar {
  transform: rotate(90deg) translate(0, 0);
  opacity: 1;
  border-radius: 3px;
}
.hamburger.is-active:before {
  transform: translate(0, -10px);
  width: 100%;
  border-radius: 3px;
}
.hamburger.is-active:after {
  transform: translate(0, 6px);
  width: 55%;
  border-radius: 3px;
}

@media (max-width: 767px) {
  nav {
    width: 100%;
  }
  .nav-container .nav-initials,
  .nav-socials {
    display: none;
  }
}
@media (min-width: 768px) {
  nav {
    flex-direction: column;
    justify-content: flex-start;
    height: 100vh;
    position: fixed;
  }
  .hamburger {
    display: none;
  }
  .links-container {
    visibility: visible;
    max-height: none;
    opacity: 1;
    transition: none;
  }
  .nav-initials {
    margin-bottom: 75px;
  }
}
