  .portfolio-grid .project-item:nth-child(n+5):nth-child(-n+6) {
    display: block;
  }
  .portfolio-grid {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
  .project-item {
    margin-bottom: 50px;
  }
  .project-item img {
